import React, { useEffect, useState } from 'react'
import LOGO from '../../../assets/LOGO.png'
import Container from '../../utils/Container'
import { StyledNav } from './Nav.styles';
import { Icon } from '@iconify/react';
import { Link } from "react-scroll";
import TopBar from '../../molecules/TopBar';
import useWindowDimensions from '../../../hooks/getWindowDimensions'

export default function Nav({contact}) {
    const [isNavOpen, setNavOpen] = useState(false);
    const [isTopBarShown, setTopBarShown] = useState(true);
    const { width: viewportWidth } = useWindowDimensions();
    const handleScroll = () => {
        if(window.pageYOffset > 100) setTopBarShown(false)
        else setTopBarShown(true)
    };
    useEffect(() => {
        window.addEventListener("scroll", handleScroll);
        return () => {
          window.removeEventListener("scroll", handleScroll);
        };
    }, []);
    const closeNav = ()=>{
        setNavOpen(false)
    }
    return (
        <>
            <StyledNav>
                <TopBar contact={contact} className={isTopBarShown ? `` : `hide`} />
                <Container className='nav'>
                    <Link
                        to="start"
                        id='logo'
                        spy={true}
                        smooth={true}
                        offset={-120}
                        tabIndex='0'
                        duration={500}>
                        <img src={LOGO} alt="Logo" />
                    </Link>
                    <ul className={isNavOpen ? `menu active` : `menu`}>
                        <Icon id='quit-btn' icon="eva:close-outline" onClick={()=>setNavOpen(false)} tabIndex='0' />
                        <li>
                            <Link
                                activeClass="active"
                                to="start"
                                spy={true}
                                smooth={true}
                                offset={-120}
                                duration={500}
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                tabIndex='0'
                                onClick={closeNav}
                            >Start</Link>
                        </li>
                        <li>
                            <Link
                                activeClass="active"
                                to="advantages"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}
                                tabIndex='0'
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                onClick={closeNav}
                            >Wie funktioniert es?</Link>
                        </li>
                        <li>
                            <Link
                                activeClass="active"
                                to="ablauf"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}
                                tabIndex='0'
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                onClick={closeNav}
                            >Ablauf</Link>
                        </li>
                        <li>
                            <Link
                                activeClass="active"
                                to="OhneMPU"
                                spy={true}
                                smooth={true}
                                offset={-70}
                                duration={500}
                                tabIndex='0'
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                onClick={closeNav}
                            >Ohne MPU</Link>
                        </li>
                        <li>
                            <Link
                                activeClass="active"
                                to="pricelist"
                                spy={true}
                                smooth={true}
                                duration={500}
                                tabIndex='0'
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                onClick={closeNav}
                            >Preisliste</Link>
                        </li>
                        <li>
                            <Link
                                activeClass="none"
                                className='btn btn-nav'
                                to="kontakt"
                                spy={true}
                                smooth={true}
                                duration={500}
                                tabIndex='0'
                                delay={viewportWidth <= 1000 ? 400 : 0}
                                onClick={closeNav}
                            >Kontakt</Link>
                        </li>
                    </ul>
                    <div id="hamburger" onClick={()=>setNavOpen(true)}>
                        <Icon icon="eva:menu-2-outline" />
                    </div>
                </Container>
            </StyledNav>
        </>
    )
}