function App() {
  const {loading, error, data} = useQuery(MASS_QUERY)
  const [newData, setNewData] = useState({
    contact: {},
    hero: {},
    advantages: "",
    whyWe: "",
    process: {},
    priceList: [],
    ohneMpu: "",
    copyright: 0
  })
  
  useEffect(()=>{
    if(data){
      setNewData({
        contact: data.contact,
        hero: data.heading,
        advantages: data.advantages.advantages.json.content[0].content[0].value,
        whyWe: data.whyWe.advantages,
        process: data.process,
        priceList: data.priceListCollection.items,
        ohneMpu: data.ohneMpu,
        copyright: data.copyright
      })
    }
  }, [data])

  if(loading) return <Loader />
  if(error) return <Loader error={true} />
  return (
    <>
      <ReactNotifications />
      <Nav contact={newData.contact} />
      <Hero data={newData.hero} />
      <Waves />
      <div style={{backgroundColor: '#F0EFF5'}}>
        <Icons data={newData.advantages} />
        <Process data={newData.process} />
        <MPU data={newData.ohneMpu.ohneMpu} />
        <PriceList data={newData.priceList} />
      </div>
      <Kontakt contact={newData.contact} />
      <Footer copyright={newData.copyright} />
    </>
  );
}

export default App;
