import { Icon } from '@iconify/react';
import React from 'react';
import styled from 'styled-components';
import Container from '../utils/Container';
import BG from '../../assets/mesh.png'
import { CUBIC_BEZIER } from '../../constants';

const TopBar = ({className, contact}) => {
    return (
        <StyledTopContact className={className}>
            <Container className="contact">
                <p><a href={`tel:${contact.phoneNumber}`}><Icon icon="eva:phone-outline" />{contact.phoneNumber}</a></p>
                <p><a href={`mailto:${contact.email}`}><Icon icon="eva:email-outline" />{contact.email}</a></p>
            </Container>
        </StyledTopContact>
    );
}

export default TopBar;
const StyledTopContact = styled.div`
    height: 44px;
    background-image: url(${BG});
    overflow: hidden;
    transition: height .4s ${CUBIC_BEZIER};
    &.hide{
        height: 0;
    }
    .contact{
        display: flex;
        align-items: center;
        gap: 1rem;
        margin: 0 auto;
        color: white;
        height: 100%;
        font-size: 14px;
        a{
            display: flex;
            align-items: center;
            gap: .3rem;
        }
        svg{
            display: grid;
            place-items: center;
            font-size: 1.2rem;
        }
    }
`;